.lr-blog-container {
	margin: 24px auto 0 auto;
	max-width: 1200px;
	display: flex;
	flex-wrap: wrap;

	@media (min-width: 1919px) {
		margin: 24px 0 0 0;
	}

	p {
		margin: 0;
	}

	.top {
		display: flex;
		width: 100%;

		.inner {
			margin-bottom: 32px;
		}
	}

	.content {
		&:nth-child(2) {
			width: calc((100% - 40px) * 0.7);
		}

		&:nth-child(3) {
			display: flex;
			flex-direction: column;
			align-items: stretch;
			margin-left: 40px;
			width: calc((100% - 40px) * 0.3);
		}
	}

	.right {
		.blog-card {
			margin-top: 40px;
			width: 100%;
		}

		> h3 {
			border-top: 2px solid #9AA6BB;
			padding: 48px 0 0 0 !important;
			margin: 0 !important;
			font-weight: 300;
			font-size: 1.71429rem;

			@media screen and (min-width:1400px) {
				font-size: 2.5714rem;
			}
		}
	}

	.left {
		p {
			img {
				display: block;
				margin: auto;
			}
		}

		.img-row {
			margin: 24px -8px 24px -8px;
			width: 100%;
			display: flex;

			img {
				flex: 1 1 0%;
				margin: 8px;
				width: 0%;
			}
		}

		.img-main {
			@extend .img-16-9;
		}

		.img-alone {
			margin: 32px 0;
			width: 100%;

			&.half {
				width: 50%;
				margin: 32px auto;
			}

			img {
				width: 100%;
				height: auto;
				object-fit: contain;
			}
		}

		.subtitle {
			font-size: 24px;
			line-height: 150%;
		}

		p {
			line-height: 200%;
		}

		ol {
			list-style-position: inside;
			line-height: 200%;
		}

		h2 {
			margin-top: 44px;
			margin-bottom: 32px;
			padding-top: 0;
		}

		p + p,
		p + ol,
		p + ul,
		ul + p,
		ol + p,
		.img-main + p {
			margin-top: 16px;
		}
	}

	@media (max-width: 1200px) {
		.content {
			&:nth-child(2) {
				width: calc((100% - 32px) * 0.7);
			}

			&:nth-child(3) {
				display: flex;
				flex-direction: column;
				align-items: stretch;
				margin-left: 32px;
				width: calc((100% - 32px) * 0.3);
			}
		}

		.left {
			.img-alone {
				margin: 24px 0;
			}

			.subtitle {
				font-size: 21px;
			}

			h2 {
				margin-top: 32px;
				margin-bottom: 24px;
			}
		}
	}

	@media (max-width: 768px) {
		.top {
			flex-direction: column;

			.inner {
				margin-bottom: 16px;
			}
		}

		.content {
			&:nth-child(2) {
				width: 100%;
			}

			&:nth-child(3) {
				margin-left: 0;
				width: 100%;
			}
		}

		.left {

			p + p,
			p + ol,
			p + ul,
			ul + p,
			ol + p {
				margin-top: 8px;
			}

			.img-row {
				margin: 16px -4px 16px -4px;

				img {
					margin: 4px;
				}
			}

			.img-main {
				// margin-top: 16px;
			}

			.img-alone {
				margin: 16px 0;
			}

			.subtitle {
				font-size: 18px;
				margin-top: 0;
			}

			h2 {
				margin-top: 24px;
				margin-bottom: 16px;
			}
		}

		.right {
			margin-left: 0;
			width: 100%;

			.blog-card {
				margin-top: 16px;
			}

			h2 {
				padding: 16px 0 0 0 !important;
			}
		}
	}
}