.blog-nav {
	display: flex;
	margin-top: 40px;

	.item {
		flex: 1 1 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-decoration: none;
		color: rgba(0, 0, 0, 0.87);
		border: 1px solid #E5E5E5;
		padding: 40px 0;
		transition: all ease .3s;
		text-align: center;

		&:hover,
		&.active {
			background: #EEEEEE;
		}

		.title {
			text-transform: uppercase;
			font-size: 18px;
			letter-spacing: 0.08em;
			line-height: 133%;
			font-weight: 500;
			margin-bottom: 7px;
		}

		.count {
			color: #48596C;
		}
	}

	@media (max-width:1200px) {
		margin-top: 24px;
	}

	@media (max-width: 768px) {
		// flex-direction: column;
		margin-top: 24px;
		overflow: auto;
		margin-right: -15px;

		.item {
			padding: 16px;
			white-space: nowrap;
			margin-right: 8px;
		}
	}
}
