.cards-grid {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto auto;
	gap: 40px;

	.item {
		@extend .blog-card;

		&:nth-child(2) {
			.img {
				@extend .img-3-4;
			}
		}

		&:first-child {
			grid-column: span 2;
			position: relative;

			&:hover {
				.info {
					background: linear-gradient(360deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 56.37%);
				}
			}

			.info {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(360deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 56.37%);
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				padding: 40px;
				text-align: left;

				.title {
					@extend h1;
					color: white;
				}

				span {
					color: white;
				}

				.link {
					color: #367C56;
				}
			}
		}
	}

	.button {
		grid-column: span 3;
		height: 64px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #367C56;
		color: white;
		text-decoration: none;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		font-weight: 700;
	}

	@media (max-width: 1200px) {
		grid-template-columns: 1fr 1fr;
		gap: 32px;

		.button {
			grid-column: span 2;
		}

		.item {
			&:nth-child(2) {
				.img {
					padding-bottom: 62.5% !important;
				}
			}
		}
	}

	@media (max-width: 768px) {
		grid-template-columns: 1fr;
		gap: 24px;

		.item {
			&:first-child {
				grid-column: initial;

				.info {
					position: static;
					background: white;
					padding: 16px;

					.title {
						font-size: 24px !important;
						text-align: center;
						color: black;
					}

					span {
						text-align: center;
						color: #48596c;
					}
				}
			}

			.info {
				.link {
					display: none;
				}
			}
		}

		.button {
			grid-column: initial;
		}
	}
}

.img-3-4 {
	padding-bottom: 75%;
	height: 0;
	position: relative;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

