@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.blog-page {
	* {
		font-family: Roboto;
		box-sizing: border-box;
		margin: 0;
		padding: 0;

		&::after,
		&::before {
			font-family: Roboto;
			box-sizing: border-box;
			margin: 0;
			padding: 0;
		}
	}

	h1 {
		font-weight: 700;
		font-size: 40px;
		line-height: 125%;

		@media (max-width: 768px) {
			font-size: 18px;
			// line-height: 278%;
		}
	}

	h2 {
		padding-top: 80px;
		margin-top: -80px;
		font-weight: 300;
		font-size: 40px;
		line-height: 147%;

		@media (max-width:1200px) {
			padding-top: 24px;
			margin-top: -24px;
		}

		@media (max-width: 768px) {
			font-size: 18px;
			font-weight: 700;
			padding-top: 160px;
			margin-top: -160px;
		}
	}

	h3 {
		font-size: 24px;
		font-weight: 500;
	}
}

h1.heading__title {
	font-weight: 700;
}
