.blog-card {
	display: flex;
	flex-direction: column;
	text-decoration: none;

	.img {
		@extend .img-8-5;
	}

	&:hover {
		.info {
			background: #EEEEEE;
		}
	}

	.info {
		transition: all ease .3s;
		height: 100%;
		border-bottom: 1px solid #E5E5E5;
		border-left: 1px solid #E5E5E5;
		border-right: 1px solid #E5E5E5;
		padding: 32px 64px;
		flex-direction: column;
		text-align: center;
		justify-content: center;
		display: flex;
		min-height: 200px;

		span {
			color: #48596C;
		}

		.title {
			@extend h3;
			margin: 8px 0;
			color: #222222;
		}

		.link {
			color: #367C56;
		}
	}

	@media (max-width: 1200px) {
		.info {
			padding: 24px;
		}
	}

	@media (max-width: 768px) {
		.info {
			padding: 16px;
			min-height: initial;
			background: white !important;
		}
	}
}